/* eslint-disable react/no-array-index-key */
import React from 'react';
import { PropTypes, describe } from 'react-desc';
import { convertPropToCssClassName } from '@elliemae/ds-classnames';

const renderPaths = (paths) => paths.map((p, i) => <path key={i} d={p} />);

function SvgIcon({
  containerProps = {},
  size,
  color,
  className = '',
  component: Component = 'span',
  viewBox = '0 0 24 24',
  paths,
  fill,
  innerRef,
  ...rest
}) {
  const { cssClassName } = convertPropToCssClassName('icon', className, {
    size,
    color,
  });
  return (
    <Component
      ref={innerRef}
      className={cssClassName}
      {...rest}
      {...containerProps}
    >
      <svg fill={fill} viewBox={viewBox}>
        {renderPaths(paths)}
      </svg>
    </Component>
  );
}

const iconProps = {
  containerProps: PropTypes.object.description(
    'set of properties attached to the main container',
  ),
  size: PropTypes.oneOf(['xxs, xs, s, m, l, xl, xxl']).description(
    'size of the icon',
  ),
  color: PropTypes.oneOf([
    ['neutral', '900'],
    ['neutral', '0'],
    ['danger', '900'],
    ['warning', '500'],
    ['success', '900'],
    ['brand-primary', '600'],
  ]).description('icon color'),
  className: PropTypes.string.description('html class attribute'),
  component: PropTypes.oneOf([PropTypes.string, PropTypes.node]).description(
    'containers element',
  ),
  viewBox: PropTypes.string.description('view box'),
  paths: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]).description(''),
  fill: PropTypes.string.description('fill attribute'),
  innerRef: PropTypes.object.description('inner ref'),
};

SvgIcon.propTypes = iconProps;

const IconWithSchema = describe(SvgIcon);
IconWithSchema.propTypes = iconProps;

export { IconWithSchema };
export default SvgIcon;
