/* eslint-disable max-lines */
import { PropTypes } from 'react-desc';

export const InstanceActionsPropTypes = PropTypes.shape({
  toggleExpandAll: PropTypes.func,
  scrollTo: PropTypes.func,
});

export const TreeViewInstancePropType = PropTypes.shape({
  current: PropTypes.oneOfType([
    PropTypes.oneOf([null]),
    PropTypes.shape({
      actions: InstanceActionsPropTypes.isRequired,
    }),
  ]),
});

export const TreeViewItemPropType = PropTypes.shape({
  children: PropTypes.arrayOf(PropTypes.object),
  id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  isGroup: PropTypes.oneOfType([
    PropTypes.oneOf([undefined, null]),
    PropTypes.bool,
  ]),
  name: PropTypes.string,
  node: PropTypes.object,
});

export const TreeViewPropTypes = {
  data: PropTypes.arrayOf(TreeViewItemPropType).description(
    'data to populate tree view',
  ).isRequired,
  plugins: PropTypes.arrayOf(PropTypes.string)
    .description('list of enabled plugins')
    .defaultValue([]),
  selection: PropTypes.object
    .description('pre selected items')
    .defaultValue({}),
  instanceRef: TreeViewInstancePropType.description('instance ref'),
  disableGroups: PropTypes.bool
    .description('disable grouping funcionality')
    .defaultValue(false),
  disableLeafs: PropTypes.bool
    .description('disable leaf items')
    .defaultValue(false),
  disableIcons: PropTypes.bool.description('disable icons').defaultValue(false),
  fluid: PropTypes.bool
    .description('whether is fluid or not')
    .defaultValue(false),
  isMultiSelect: PropTypes.bool
    .description('whether the tree view is multi select or not')
    .defaultValue(false),
  isSingleSelect: PropTypes.bool
    .description('wheter the tree view is single select or not')
    .defaultValue(false),
  restrictDragAndDrop: PropTypes.bool
    .description('whether to restrict DnD functionality or not')
    .defaultValue(false),
  showChildrenAmount: PropTypes.bool
    .description('whether to show the amount of children or not')
    .defaultValue(false),
  sortable: PropTypes.bool
    .description('whether the sortable functionality is active or not')
    .defaultValue(false),
  labelRenderer: PropTypes.func.description('custom renderer for labels'),
  onItemClick: PropTypes.func.description(
    'function executed when clicking on an item',
  ),
  onItemFocus: PropTypes.func.description(
    'function executed when focusing on an item',
  ),
  onVisibleItemsChange: PropTypes.func.description(
    'function executed when the visible items change',
  ),
  onOrderChange: PropTypes.func.description(
    'function executed when the items order changes',
  ),
  onSelectionChange: PropTypes.func.description(
    'function executed when the selection changes',
  ),
  onExpandChange: PropTypes.func.description(
    'function executed when expanding an item',
  ),
  rowSize: PropTypes.oneOf(['normal', 'compact'])
    .description('size of the row')
    .defaultValue('normal'),
  width: PropTypes.oneOfType([PropTypes.number, PropTypes.string])
    .description('components width')
    .defaultValue('100%'),
  height: PropTypes.oneOfType([PropTypes.number, PropTypes.string])
    .description('components height')
    .defaultValue('100%'),
  groupIcon: PropTypes.node.description('icon for groups'),
  itemIcon: PropTypes.node.description('icon for items'),
  searchQuery: PropTypes.string.description('search query value'),
  highlightOnlyQuery: PropTypes.bool
    .description('whether to highlight results or not')
    .defaultValue(false),
};
export const TreeListPropTypes = {
  // required when drag and drop is enabled, tracks the drag and drop results after drop and visual feedback borders
  // passed down to TreeItem
  onMouseDragOverItem: PropTypes.func,
};
export const TreeItemPropTypes = {
  item: TreeViewItemPropType.isRequired,
  index: PropTypes.number,
  itemIndex: PropTypes.number,
  itemWrapperStyle: PropTypes.object,
  virtualItemRef: PropTypes.func,
  spaceForNestingLevel: PropTypes.number,
  onMouseDragOverItem: PropTypes.func,
};

export const TreeItemExpandCaretPropTypes = {
  item: TreeViewItemPropType.isRequired,
};
export const TreeItemNestingSpacePropTypes = {
  item: TreeViewItemPropType.isRequired,
  spaceForNestingLevel: PropTypes.number.isRequired,
};
export const TreeItemCheckboxPropTypes = {
  item: TreeViewItemPropType.isRequired,
  itemIndex: PropTypes.number.isRequired,
};
